var token=""
export default {

	data() {
		return {
			goodsList: [],
			searchKey: [],
			ipt:'',
			searchClose: true,
			jiaodian:true,
			isShowHist:false,
		};
	},

	onShow() {
		token = uni.getStorageSync("token")
		try{
		    var vv = uni.getStorageSync('searchLocal') || "";
		    if(vv) {
		        this.searchKey = JSON.parse(vv);
		    } else {
		        this.searchKey = [];
		    }
		} catch(e){
		    console.log(e)
		}
	},
	methods: {
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			console.log(item)
			uni.navigateTo({
				url: `/pages/product/product?id=${item}`
			})
		},
		 clearKey: function() {
		                var that = this;
		                uni.showModal({
		                    title: '提示',
		                    content: '点击确定将删除所有历史信息，确定删除吗？',
		                    success: function(res) {
		                        // if (res.confirm) {
		                            that.searchKey = [];
		                            uni.setStorage({
		                                key: 'searchLocal',
		                                data: that.searchKey
		                            });
		//                      } else if (res.cancel) {
		//
		//                      }
		                    }
		                });
		 
		            },
		           
		            qusousuo(e){
		                console.log(e);
		                this.ipt = e;
		               
		            },
				srcSub()
				{
			
			                if (this.ipt == '') {
			                    uni.showToast({
			                        title: '未输入搜索关键字',
			                        icon: 'none',
			                        duration: 1000
			                    });
			                    return false;
			                }
							this.isShowHist = true
			                var that = this;
			                var newArr = that.searchKey;
			                console.log(newArr.length)
			                if(this.searchKey.indexOf(this.ipt) == -1){
			                    newArr.unshift(this.ipt);
			                }
			                if(newArr.length >= 11 ){
			                    newArr.pop()
			                }
			                console.log(newArr.length)
			                newArr = JSON.stringify(newArr);//转换成json格式的数据
			                uni.setStorage({
			                    key: 'searchLocal',
			                    data: newArr
			                });
							this.loadGoods()
				},
				car(goodsId){
					var _this = this
					uni.request({
					    url:this.$url+'/car/add' ,//服务器端地址
					    data: {
							goodsId:goodsId
					    },
					    method: 'POST',
					    header: {
					        'content-type': 'application/json',
							'token':token,
					    },
					    success: (res) => { 
							console.log(res.data)
					        if (res.data.code == 200) {
								this.$api.msg('已添加购物车');
					        }
					    }
					   
					});
				},
		loadGoods()
		{
			var _this = this
			uni.request({
			    url:this.$url+'/goods/search' ,//服务器端地址
			    data: {
					keywords:_this.ipt,
					pageNum:"0",
					pageSize:"10",
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.goodsList = res.data.data.content
			        }
					
			    }
			   
			});
		},
		
	},

























}
